:root {
  --black: hsl(0, 0%, 0%);
  --white: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  background-color: red;
  margin: 0;
  padding: 0;
}
body.noscroll {
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-ai-b {
  align-items: baseline;
}
.flex-d-c {
  flex-direction: column;
}
.flex-d-r {
  flex-direction: row;
}

@media (max-width: 49.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 50em) {
  .hide-for-desktop {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.img-responsive {
  max-width: 100%;
  height: auto;
}

button {
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

input[type=text] {
  border: none;
  border-radius: 0;
  outline: none;
}
input[type=text]:focus {
  border: none;
}

.border-r-all {
  border-radius: var(--border-radius-size);
}
.border-r-bottom {
  border-bottom-left-radius: var(--border-radius-size);
  border-bottom-right-radius: var(--border-radius-size);
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  color: var(--very-light-gray-l);
  letter-spacing: 0.625rem;
  font-size: 1.375rem;
}
@media (min-width: 50em) {
  h1 {
    letter-spacing: 1rem;
    font-size: 2rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  margin: 0;
}

.img-responsive {
  width: 100%;
  max-width: 100%;
  vertical-align: middle;
  border-style: none;
}